<template>
    <yd-layout>
        <div class="mainContent" v-if="liuyan.length!=0">
            <yd-cell-group>
                <yd-cell-item>
                    <span slot="left" class="lastPost">上一则附言：</span>
                    <div slot="right" class="fyColor">{{liuyan.neirong}}</div>
                </yd-cell-item>
            </yd-cell-group>
        </div>
        <div class="mainContentFluid" v-if="$route.params.agent!='false'">
            <div class="agent1">
                <div class="adviceTxt">
                    <span slot="left" class="adviceLeft">填写附言：</span>
                    <trsTextarea placeholder="请输入附言内容..." v-model="fuyan"></trsTextarea>
                </div>
            </div>
        </div>
        <div class="mainContentFluid agent2">
            <yd-flexbox direction="vertical">
                <yd-flexbox-item v-if="bossKey == 'bossActivity'">
                    <div class="leaderExamine" v-bind:class="{active:officeActive==1}" v-on:click="officeActive=1;leaderIDs=bossleaderIDs;activityID='bossActivity'">送领导审批（{{bossName}}）</div>
                </yd-flexbox-item>
                <yd-flexbox-item v-if="acceptKey == 'acceptActivity'">
                    <div class="leaderExamine" v-bind:class="{active:officeActive==2}" v-on:click="officeActive=2;leaderIDs=acceptleaderIDs;activityID='acceptActivity'">人力资源受理人（{{acceptName}}）</div>
                </yd-flexbox-item>
                <yd-flexbox-item v-if="applyKey == 'applyActivity'">
                    <div class="leaderExamine" v-bind:class="{active:officeActive==3}" v-on:click="officeActive=3;leaderIDs=applyLeaderIDs;activityID='applyActivity'">返回申请人（{{applyName}}）</div>
                </yd-flexbox-item>
                <yd-flexbox-item v-if="logoutKey == 'logoutActivity'">
                    <div class="leaderExamine" v-bind:class="{active:officeActive==4}" v-on:click="officeActive=4;leaderIDs=logoutLeaderIDs;activityID='logoutActivity'">销假（{{logoutName}}）</div>
                </yd-flexbox-item>
                <yd-flexbox-item v-if="doKey == 'doApprove'">
                    <div class="leaderExamine" v-bind:class="{active:officeActive==5}" v-on:click="statusPopup=true;popupArr=approver;officeActive=999;confirmItem='';confirmItem2=''">送领导审批{{confirmItem}}</div>
                </yd-flexbox-item>
                <yd-flexbox-item v-if="secretaryKey == 'secretaryActivity'">
                    <div class="leaderExamine" v-bind:class="{active:officeActive==6}" v-on:click="statusPopup2=true;popupArr2=secretaryList;officeActive=999;confirmItem='';confirmItem2='';activityID='secretaryActivity'">送秘书预审{{confirmItem2}}</div>
                </yd-flexbox-item>
            </yd-flexbox>
        </div>
        <yd-tabbar slot="tabbar"  v-if="statusPopup2!=true && statusPopup != true">
            <div class="assistBottom">
                <div class="examineLogo btnOne">
                    <yd-flexbox>
                        <div class="examineLogo_txt toBack" @click="toBack(9)">返回</div>
                    </yd-flexbox>
                </div>
                <div class="examineLogo btnTwo" v-on:click="sendBtn()">
                    <yd-flexbox>
                        <div class="examineLogo_txt">确认发送</div>
                    </yd-flexbox>
                </div>
            </div>
        </yd-tabbar>
        <yd-popup v-model="statusPopup" position="bottom" height="4rem">
            <div class="confirmBtn" slot="top">
                <div class="leftBtn" v-on:click="statusPopup = false">取消</div>
                <div class="rightBtn" v-on:click="statusPopup = false;officeActive=5;selectName()">确定</div>
            </div>
            <div class="selectStatus">
                <ul class="ul">
                    <li class="li" v-for="(item,index) in popupArr" v-bind:class="{active:statusRadio==index}" v-on:click="statusRadio=index;itemname=item.empname;leaderIDs=item.operatorid;" v-text='item.empname'>
                    </li>
                </ul>
            </div>
        </yd-popup>
        <yd-popup v-model="statusPopup2" position="bottom" height="4rem">
            <div class="confirmBtn" slot="top">
                <div class="leftBtn" v-on:click="statusPopup2 = false">取消</div>
                <div class="rightBtn" v-on:click="statusPopup2 = false;officeActive=6;selectName2()">确定</div>
            </div>
            <div class="selectStatus">
                <ul class="ul">
                    <li class="li" v-for="(item,index) in popupArr2" v-bind:class="{active:statusRadio2==index}" v-on:click="statusRadio2=index;itemname=item.empname;leaderIDs=item.operatorid;" v-text='item.empname'>
                    </li>
                </ul>
            </div>
        </yd-popup>
    </yd-layout>
</template>
<script>
import { mapGetters } from 'vuex';
export default {
    name: 'assistantSend',
    data() {
        return {
            officeActive: 999,
            statusPopup: false,
            statusPopup2: false,
            statusRadio: 999,
            statusRadio2: 999,
            liuyan: [],
            fuyan: '',
            bossKey: '',
            acceptKey: '',
            bossleaderIDs: '',
            acceptleaderIDs: '',
            bossName: '',
            acceptName: '',
            leaderIDs: '',
            activityID: '',
            applyKey: '',
            applyLeaderIDs: '',
            applyName: '',
            logoutKey: '',
            logoutLeaderIDs: '',
            logoutName: '',
            doKey: '',
            doName: '',
            popupArr: [],
            popupArr2: [],
            confirmItem: '',
            confirmItem2: '',
            itemname: '',
            approveType: '',
            secretaryKey: ''
        }
    },
    props: {
        nextBtn: {
            type: Object
        },
        approver: {
            type: Array
        },
        secretaryList: {
            type: Array
        },
        json: {
            type: Object
        }
    },
    mounted() {
        document.setTitle('请休假');
        this.initData();
        this.lastPost();
    },
    computed: {
        ...mapGetters({
            userInfo: 'getUserInfo'
        })
    },
    methods: {
        toBack(type) {
            this.$emit('backSetflag', type);
        },
        initData() {
            let vm = this;
            for (let key in vm.nextBtn) {
                if (key.indexOf('bossActivity') != -1) {
                    vm.bossKey = key;
                    vm.bossleaderIDs = vm.nextBtn[key][0];
                    vm.bossName = vm.nextBtn[key][1];
                } else if (key.indexOf('acceptActivity') != -1) {
                    vm.acceptKey = key;
                    vm.acceptleaderIDs = vm.nextBtn[key][0];
                    vm.acceptName = vm.nextBtn[key][1];
                } else if (key.indexOf('applyActivity') != -1) {
                    vm.applyKey = key;
                    vm.applyLeaderIDs = vm.nextBtn[key][0];
                    vm.applyName = vm.nextBtn[key][1];
                } else if (key.indexOf('logoutActivity') != -1) {
                    vm.logoutKey = key;
                    vm.logoutLeaderIDs = vm.nextBtn[key][0];
                    vm.logoutName = vm.nextBtn[key][1];
                } else if (key.indexOf('doApprove') != -1) {
                    vm.doKey = key;
                    for (let keyJ in vm.nextBtn.doApprove) {
                        if (keyJ.indexOf('type') != -1) {
                            vm.approveType = vm.nextBtn.doApprove[keyJ];
                        }
                    }
                } else if (key.indexOf('secretaryActivity') != -1) {
                    vm.secretaryKey = key;
                }
            }
        },
        sendBtn() {
            let vm = this;
            if (vm.leaderIDs == '') {
                vm.$dialog.toast({
                    mes: '请选择发送人',
                    timeout: 2000
                });
            } else {
                if (vm.officeActive == 5) {
                    vm.post(vm.conf.portalMobile + '/API/WorkFlow/oawf/doApprove', {
                        'type': 'askforleave',
                        'approveType': vm.approveType,
                        'workItemID': vm.$route.params.workId,
                        'token': `${vm.userInfo.ssotoken}L/wp-2q${vm.userInfo.ssotokenCode}`,
                        'leaderIDs': vm.leaderIDs,
                        'fuyan': vm.fuyan,
                        'opinion': '',
                        'workTitle': vm.json.firstorgname + vm.json.appLyName + '请假申请',
                        'ext': ''
                    }, function (res) {
                        vm.$dialog.toast({
                            mes: '提交申请成功',
                            timeout: 2000,
                            callback: () => {
                                vm.$router.push('/handle/');
                            }
                        });
                    });
                } else {
                    vm.get(vm.conf.portalMobile + '/API/WorkFlow/oawf/doOANext', {
                        'type': 'askforleave',
                        'workItemID': vm.$route.params.workId,
                        'token': `${vm.userInfo.ssotoken}L/wp-2q${vm.userInfo.ssotokenCode}`,
                        'activityID': vm.activityID,
                        'leaderIDs': vm.leaderIDs,
                        'fuyan': vm.fuyan,
                        'opinion': '',
                        'workTitle': vm.json.firstorgname + vm.json.appLyName + '请假申请',
                        'ext': ''
                    }, function (res) {
                        vm.$dialog.toast({
                            mes: '发送成功',
                            timeout: 2000,
                            callback: () => {
                                vm.$router.push('/handle/');
                            }
                        });
                    });
                }
            }
        },
        selectName() {
            let vm = this;
            vm.confirmItem = '（' + vm.itemname + '）';
        },
        selectName2() {
            let vm = this;
            vm.confirmItem2 = '（' + vm.itemname + '）';
        },
        // 上一则附言
        lastPost() {
            let vm = this;
            vm.get(vm.conf.portalMobile + '/API/WorkFlow/oawf/oapreviousfuyan', {
                'type': 'askforleave',
                'workItemID': vm.$route.params.workId,
                'token': `${vm.userInfo.ssotoken}L/wp-2q${vm.userInfo.ssotokenCode}`
            }, function (res) {
                if (res.jsonData != '') {
                    vm.liuyan = res.jsonData[0];
                }
            });
        }
    }
}
</script>
<style scoped>
.fluidWidth2 {
    padding: 0;
}

.fluidWidth {
    padding-left: 0;
    padding-right: 0;
}

.lastPost {
    color: #333;
    font-size: 0.32rem;
}

.yd-cell-box {
    border-bottom: 1px solid #eaeaea;
    margin-bottom: 0;
}

.agent2 {
    margin-top: 0;
    border-top: .1rem solid #eaeaea;
}
.agent2 .active {
    color: #333;
    /*font-weight: bold;*/
}

.agent2 .active:after {
    content: '';
    height: .45rem;
    width: .4rem;
    display: inline-block;
    background: url(../../../static/images/trs_up_03.png) no-repeat;
    background-position: 0 .14rem;
    background-size: 0.4rem;
    position: absolute;
    top: 0.22rem;
    right: 0.4rem;
}

.toTop {
    margin-top: 0.3rem;
}

.yd-cell-item {
    padding-left: 0.1rem;
}

.leftText {
    color: #333;
    font-size: 0.32rem;
}

.sendGround {
    background-color: #fff;
}

.fyColor {
    width: 100%;
    text-align: left;
    color: #999;
    font-size: 0.32rem;
}

.assistBottom {
    border-top: none;
}

.examineLogo {
    height: 0.99rem;
    margin-top: 0;
    border-right: none;
}

.examineLogo .yd-flexbox {
    line-height: 0.99rem;
}

.examineLogo_txt {
    color: #fff;
    font-size: 0.36rem;
    font-weight: normal;
}

.btnOne {
    background: #fafafa;
    letter-spacing: 0.1rem;
    border: 1px solid #eaeaea;
}

.btnTwo {
    background-color: #46a4f0;
}

.yd-tabbar {
    padding: 0;
}

.yd-tabbar:after {
    border-top: none;
}

.toBack {
    color: #333;
}

.mainContent {
    height: auto;
}

.agent1 {
    padding: 0 .33rem;
    padding-top: .27rem;
    padding-bottom: .27rem;
}

.adviceLeft {
    font-size: 0.32rem;
    color: #333;
    height: 0.6rem;
    line-height: 0.6rem;
    display: block;
}

.trsTextarea {
    overflow: auto;
    word-break: break-all;
    outline: none;
    user-select: text;
    white-space: pre-wrap;
    text-align: left;
    font-size: 0.32rem;
    color: #999;
    min-height: 2rem;
    margin-top: 0.2rem;
}

.leaderExamine {
    padding-left: 0;
    margin-top: 0;
    border-bottom: none;
    padding-bottom: 0;
    color: #666;
}

.yd-flexbox-item {
    padding: 0 .33rem;
    height: 1.09rem;
    line-height: 1.09rem;
    border-bottom: 1px dashed #eaeaea;
}

.yd-flexbox-item:last-child {
    border-bottom: none;
}
</style>


