<template>
    <yd-layout class="groundColor">
        <div v-show="approFlag">
            <div class="mainContent">
                <div class="contentSradius holiday">
                    <div class="holiday_one aproveLine">
                        <yd-flexbox direction="vertical">
                            <yd-flexbox-item>
                                <yd-flexbox>
                                    <div class="hint">
                                        {{json.firstorgname}}{{json.appLyName}}的请假申请
                                    </div>
                                </yd-flexbox>
                            </yd-flexbox-item>
                        </yd-flexbox>
                    </div>
                    <div class="holiday_two">
                        <i class="circle01"></i>
                        <i class="circle02"></i>
                        <yd-flexbox direction="vertical">
                            <yd-flexbox-item v-if="json.firstorgname!=''">
                                <yd-flexbox>
                                    <div class="leftWord">
                                        所在科室：
                                    </div>
                                    <div class="rightWord" v-text="json.orgname"></div>
                                </yd-flexbox>
                            </yd-flexbox-item>
                            <yd-flexbox-item>
                                <yd-flexbox>
                                    <div class="leftWord">
                                        请假类型：
                                    </div>
                                    <div class="rightWord">年假</div>
                                </yd-flexbox>
                            </yd-flexbox-item>
                            <yd-flexbox-item>
                                <yd-flexbox>
                                    <div class="leftWord">
                                        请假时间：
                                    </div>
                                    <div class="rightWord">{{formatDate(json.startDate, 'yyyy-MM-dd')}}至{{formatDate(json.endDate, 'yyyy-MM-dd')}}</div>
                                </yd-flexbox>
                            </yd-flexbox-item>
                            <yd-flexbox-item>
                                <yd-flexbox>
                                    <div class="leftWord">
                                        时长(天) ：
                                    </div>
                                    <div class="rightWord" v-text="json.days"></div>
                                </yd-flexbox>
                            </yd-flexbox-item>
                            <yd-flexbox-item v-if="json.isOut!=''&&(json.applyLevel=='2'||json.orgname=='总经理室')">
                                <yd-flexbox>
                                    <div class="leftWord">
                                        出国（境）：
                                    </div>
                                    <div class="rightWord" v-if="json.isOut=='0'">否</div>
                                    <div class="rightWord" v-if="json.isOut=='1'">是</div>
                                </yd-flexbox>
                            </yd-flexbox-item>
                            <yd-flexbox-item v-if="json.outDate!=''&&(json.applyLevel=='2'||json.orgname=='总经理室')">
                                <yd-flexbox>
                                    <div class="leftWord">
                                        离境时间：
                                    </div>
                                    <div class="rightWord" v-text="formatDate(json.outDate, 'yyyy-MM-dd')"></div>
                                </yd-flexbox>
                            </yd-flexbox-item>
                            <yd-flexbox-item v-if="json.enterDate!=''&&(json.applyLevel=='2'||json.orgname=='总经理室')">
                                <yd-flexbox>
                                    <div class="leftWord">
                                        入境时间：
                                    </div>
                                    <div class="rightWord" v-text="formatDate(json.enterDate, 'yyyy-MM-dd')"></div>
                                </yd-flexbox>
                            </yd-flexbox-item>
                            <yd-flexbox-item v-if="json.agentname!=''">
                                <yd-flexbox>
                                    <div class="leftWord">
                                        工作代理人：
                                    </div>
                                    <div class="rightWord" v-text="json.agentname"></div>
                                </yd-flexbox>
                            </yd-flexbox-item>
                            <yd-flexbox-item v-if="json.address!=''&&(json.applyLevel=='1'||json.applyLevel=='2'||json.orgname=='总经理室')">
                                <yd-flexbox>
                                    <div class="leftWord">
                                        休假地点：
                                    </div>
                                    <div class="rightWord" v-text="json.address"></div>
                                </yd-flexbox>
                            </yd-flexbox-item>
                            <yd-flexbox-item class="remarkItem" v-if="json.remark!=''">
                                <yd-flexbox>
                                    <div class="leftWord remarkLeft">
                                        请假事由：
                                    </div>
                                    <div class="rightWord remarkRight" v-html="json.remark"></div>
                                </yd-flexbox>
                            </yd-flexbox-item>
                        </yd-flexbox>
                    </div>
                    <div class="holiday_three">
                        <i class="circle01"></i>
                        <i class="circle02"></i>
                        <yd-flexbox direction="vertical">
                            <yd-flexbox-item>
                                <yd-flexbox>
                                    <div class="leftWord">
                                        剩余年假：
                                    </div>
                                    <div class="rightWord">{{numDays}}天</div>
                                </yd-flexbox>
                                <yd-flexbox>
                                    <div class="leftWord">
                                        请假次数：
                                    </div>
                                    <div class="rightWord">{{times}}次</div>
                                </yd-flexbox>
                            </yd-flexbox-item>
                            <yd-flexbox-item>
                                <yd-flexbox>
                                    <div class="lookHistory" @click="jumpHistory()">
                                        查看历史休假记录
                                    </div>
                                </yd-flexbox>
                            </yd-flexbox-item>
                        </yd-flexbox>
                    </div>
                </div>
            </div>
            <div class="mainContentFluid">
                <div class="historyWidth">
                    <div class="approHistory">
                        <div class="title">
                            审批记录
                        </div>
                        <div class="noRecord" v-show="approvelog.length==0">暂无审批记录</div>
                        <div v-for="(item, index) in approvelog" :key="item.partiname" v-show="approvelog.length>0">
                            <yd-flexbox>
                                <div class="approName" v-if="item.activityDefName!='领导秘书预审'">{{item.partiname}}：</div>
                                <yd-flexbox-item class="neirongSize" v-html="transferString(item.neirong)"></yd-flexbox-item>
                            </yd-flexbox>
                            <yd-flexbox>
                                <div class="approTime">{{formatDate(item.submitDate, 'yyyy-MM-dd')}}</div>
                                <div class="approTime">{{formatDate(item.submitDate, 'hh:mm')}}</div>
                            </yd-flexbox>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <div class="last_send02" v-if="agentFlag">
            <agent-Enter :formData="formData" :nextBtn="nextBtn" v-on:backSetflag="setFlag"></agent-Enter>
        </div>
        <div class="last_send02" v-if="sendFlag">
            <send-Page :json="json" :secretaryList="secretaryList" :approver="approver" :nextBtn="nextBtn" v-on:backSetflag="setFlag"></send-Page>
        </div>
        <yd-tabbar slot="tabbar" v-if="status==0 && approFlag && agent==undefined">
            <div class="assistBottom">
                <div class="otherL otherL_agree">
                    <a href="javascript:;">
                        <yd-flexbox>
                            <div class="examineLogo_txt" v-text="agree" v-on:click="opinion=agree;leaderApprove()"></div>
                        </yd-flexbox>
                    </a>
                </div>
                <div class="otherL otherL_disagree">
                    <a href="javascript:;">
                        <yd-flexbox>
                            <div class="examineLogo_txt" v-text="disagree" v-on:click="opinion=disagree;leaderApprove()"></div>
                        </yd-flexbox>
                    </a>
                </div>
                <div class="circulationLogo">
                    <yd-flexbox v-on:click.native="show = true">
                        <div class="examineLogo_txt">其他意见</div>
                    </yd-flexbox>
                </div>
            </div>
        </yd-tabbar>
        <yd-tabbar slot="tabbar" v-if="status==0 && approFlag  && agent=='true'">
            <div class="assistBottom">
                <div class="agentLeader">
                    <yd-flexbox>
                        <div class="examineLogo_txt01" v-on:click="setFlag(1)">代领导录入</div>
                    </yd-flexbox>
                </div>
                <div class="agentLeader">
                    <yd-flexbox>
                        <div class="examineLogo_txt02" v-on:click="setFlag(2)">发送</div>
                    </yd-flexbox>
                </div>
            </div>
        </yd-tabbar>
        <yd-tabbar slot="tabbar" v-if="status==0 && approFlag  && agent=='false'">
            <div class="footer">
                <yd-button v-on:click.native="setFlag(2)" size="large" type="primary" bgcolor="#00B6FC" color="#fefefe">发送</yd-button>
            </div>
        </yd-tabbar>
        <yd-popup v-model="show" position="center" width="5.79rem">
            <div class="other">其他意见</div>
            <yd-cell-group class="otherInput">
                <yd-cell-item>
                    <yd-textarea slot="right" v-model="otherSuggest" placeholder="请您输入意见..."></yd-textarea>
                </yd-cell-item>
            </yd-cell-group>
            <div class="otherBtn">
                <ul>
                    <li class="yes" @click="show=false;opinion=otherSuggest;leaderApprove()">确定</li>
                    <li class="no" @click="show=false">取消</li>
                </ul>
            </div>
        </yd-popup>
    </yd-layout>
</template>
<script>
import { mapGetters } from 'vuex';
import agentEnter from './agentEnter.vue';
import sendPage from './sendPage.vue';
export default {
    name: 'approveHoliday',
    components: {
        agentEnter,
        sendPage
    },
    data() {
        return {
            show: false,
            agree: '同意',
            disagree: '不同意',
            otherSuggest: '',
            json: '',
            opinion: '',
            approveType: '',
            days: '',
            numDays: '',
            holidayType: ['年休假', '病假', '事假', '婚假', '产前假', '产假', '工伤假'],
            leavetypeNum: ['1', '2', '3', '4', '5', '6', '10'],
            confirmItem: '',
            department: '',
            flag: true,
            state: '',
            leaderIDs: '',
            startdate: '',
            enddate: '',
            userid: '',
            status: this.$route.params.status,
            agent: this.$route.params.agent,
            approFlag: true,
            agentFlag: false,
            sendFlag: false,
            historyList: [],
            approvelog: [],
            doOrNext: '',
            nextActivity: '',
            nextBtn: {},
            queryTypeArr: [23, 24, 25],
            approver: [],
            timeArry: [],
            times: '',
            thisYear: '',
            formData: {
                firstorgname: '',
                applicantname: ''
            },
            secretaryList: [],
            errorTime: 0
        }
    },
    mounted() {
        document.setTitle('请休假');
        let vm = this;
        vm.initData();
        vm.approveLog();
        vm.getApprover();
        vm.getSecretary();
        vm.errorTime = 0;
        vm.$dialog.loading.open('加载中...');
    },
    computed: {
        ...mapGetters({
            userInfo: 'getUserInfo'
        })
    },
    methods: {
        // 表单数据
        initData() {
            var vm = this;
            var workId = this.$route.params.workId;
            vm.getM(vm.conf.portalMobile + '/API/WorkFlow/oawf/queryForm', {
                'type': 'askforleave',
                'workItemID': workId,
                'token': `${vm.userInfo.ssotoken}L/wp-2q${vm.userInfo.ssotokenCode}`
            }, function(res) {
                if (res.isSuccess == 'false') {
                    vm.errorTime++;
                    if (vm.errorTime < 3) {
                        vm.initData();
                    } else {
                        vm.$dialog.loading.close();
                        vm.$dialog.toast({
                            mes: '表单未加载成功',
                            timeout: 2000,
                            callback: () => {
                                vm.$router.push('/handle');
                            }
                        });
                    }
                } else if (res.isSuccess == 'true') {
                    vm.$dialog.loading.close();
                }
                if (res.jsonData.CURRENTSTATE != 10 && res.jsonData.CURRENTSTATE != 4 && vm.$route.params.status == 0 && res.isSuccess == 'true') {
                    vm.$dialog.toast({
                        mes: '该流程已被处理',
                        timeout: 2000,
                        callback: () => {
                            setTimeout(function() {
                                if (vm.$route.params.status == 0) {
                                    vm.$router.push('/handle');
                                }
                            })
                        }
                    });
                    return false;
                }
                vm.json = res.jsonData;
                vm.userid = res.jsonData.applicantid;
                vm.startdate = res.jsonData.startDate;
                vm.enddate = res.jsonData.endDate;
                vm.days = res.jsonData.days;
                vm.leavetype = vm.json.leavetype;
                vm.nextBtn = res.jsonData.nextBtn;
                var leavetypeIndex = vm.leavetypeNum.indexOf(vm.leavetype);
                vm.confirmItem = vm.holidayType[leavetypeIndex];
                vm.department = vm.json.firstorgname;
                vm.formData.firstorgname = res.jsonData.firstorgname;
                vm.formData.applicantname = res.jsonData.appLyName;
                vm.annualLeave();
                for (let keyJ in res.jsonData.nextBtn) {
                    if (keyJ == 'doApprove') {
                        vm.doOrNext = 'doApprove';
                        if (!res.jsonData.nextBtn.doApprove.queryHandlerPerson) {
                            for (let keyId in res.jsonData.nextBtn.doApprove) {
                                if (keyId != 'type') {
                                    vm.leaderIDs = keyId;
                                } else if (keyId == 'type') {
                                    vm.approveType = res.jsonData.nextBtn.doApprove.type;
                                }
                            }
                        }
                    } else if (keyJ.indexOf('Activity') != -1) {
                        vm.leaderIDs = res.jsonData.nextBtn[keyJ][0];
                        vm.nextActivity = keyJ;
                    }
                }
            }, function(res) {
                if (res.response.status != 200) {
                    vm.errorTime++;
                    if (vm.errorTime < 3) {
                        vm.initData();
                        console.log(vm.errorTime);
                    } else {
                        vm.$dialog.loading.close();
                        vm.$dialog.toast({
                            mes: '表单未加载成功',
                            timeout: 2000,
                            callback: () => {
                                vm.$router.push('/handle');
                            }
                        });
                    }
                }
            });
        },
        // 年假天数信息
        annualLeave() {
            var vm = this;
            vm.get(vm.conf.portalMobile + '/API/AnnLeave/annual/getAnnualLeave', {
                'token': `${vm.userInfo.ssotoken}L/wp-2q${vm.userInfo.ssotokenCode}`,
                'otherUserName': vm.userid
            }, function(res) {
                var data = res.jsonData[0];
                vm.numDays = data.lastyearall - data.lastyearthen + data.yearall - data.yearthen;
                vm.thisYear = data.alyear;
                vm.historyData();
            }, function(res) {}, false);
        },
        // 历史休假列表
        historyData() {
            let vm = this;
            vm.get(vm.conf.portalMobile + '/API/WorkFlow/oawf/historyRecord', {
                'type': 'askforleave',
                'token': `${vm.userInfo.ssotoken}L/wp-2q${vm.userInfo.ssotokenCode}`,
                'otherUserName': vm.userid,
                'ext': '{"queryType":"1","queryYear":"' + vm.thisYear + '","queryState":"all"}'
            }, function(res) {
                vm.historyList = res.jsonData;
                for (let i = 0; i < res.jsonData.length; i++) {
                    let item = res.jsonData[i];
                    if (item.state != '0') {
                        vm.timeArry.push(item);
                    }
                }
                vm.times = vm.timeArry.length;
            }, function(res) {}, false);
        },
        // 审批记录
        approveLog() {
            var vm = this;
            vm.post(vm.conf.portalMobile + '/API/WorkFlow/oawf/queryApprove', {
                'type': 'askforleave',
                'workItemID': vm.$route.params.workId,
                'token': `${vm.userInfo.ssotoken}L/wp-2q${vm.userInfo.ssotokenCode}`,
                'random': Math.floor(Math.random() * (100000 + 1))
            }, function(res) {
                if (res.jsonData != '') {
                    vm.approvelog = res.jsonData;
                }
            }, function(res) {}, false);
        },
        // 领导审批
        leaderApprove() {
            var vm = this;
            if (vm.doOrNext == 'doApprove') {
                vm.post(vm.conf.portalMobile + '/API/WorkFlow/oawf/doApprove', {
                    'type': 'askforleave',
                    'approveType': vm.approveType,
                    'workItemID': vm.$route.params.workId,
                    'token': `${vm.userInfo.ssotoken}L/wp-2q${vm.userInfo.ssotokenCode}`,
                    'leaderIDs': vm.leaderIDs,
                    'fuyan': '',
                    'opinion': vm.opinion,
                    'workTitle': vm.json.firstorgname + vm.json.appLyName + '请假申请',
                    'ext': ''
                }, function(res) {
                    vm.$dialog.toast({
                        mes: '审批完成',
                        timeout: 2000,
                        callback: () => {
                            vm.$router.push('/handle/');
                        }
                    });
                });
            } else {
                vm.post(vm.conf.portalMobile + '/API/WorkFlow/oawf/doOANext', {
                    'type': 'askforleave',
                    'workItemID': vm.$route.params.workId,
                    'token': `${vm.userInfo.ssotoken}L/wp-2q${vm.userInfo.ssotokenCode}`,
                    'activityID': vm.nextActivity,
                    'leaderIDs': vm.leaderIDs,
                    'fuyan': '',
                    'opinion': vm.opinion,
                    'workTitle': vm.json.firstorgname + vm.json.appLyName + '请假申请',
                    'ext': ''
                }, function(res) {
                    vm.$dialog.toast({
                        mes: '审批完成',
                        timeout: 2000,
                        callback: () => {
                            vm.$router.push('/handle/');
                        }
                    });
                });
            }
        },
        // 跳转到历史记录页面
        jumpHistory() {
            let vm = this;
            vm.$router.push('/history/false/' + vm.$route.params.workId);
        },
        // flag显示
        setFlag(type) {
            let vm = this;
            if (type == 1) {
                vm.approFlag = false;
                vm.agentFlag = true;
                vm.sendFlag = false;
            } else if (type == 2) {
                vm.approFlag = false;
                vm.agentFlag = false;
                vm.sendFlag = true;
            } else if (type == 9) {
                vm.approFlag = true;
                vm.agentFlag = false;
                vm.sendFlag = false;
            }
        },
        // 查询审批人
        getApprover() {
            let vm = this;
            if (vm.agent == 'false') {
                for (var i = 0; i < vm.queryTypeArr.length; i++) {
                    var item = vm.queryTypeArr[i];
                    vm.post(vm.conf.portalMobile + '/API/WorkFlow/oawf/handlerPerson', {
                        'type': 'travelremark',
                        'token': `${vm.userInfo.ssotoken}L/wp-2q${vm.userInfo.ssotokenCode}`,
                        'queryType': item
                    }, function(res) {
                        var data = res.jsonData;
                        for (var j = 0; j < data.length; j++) {
                            var item2 = data[j];
                            vm.approver.push(item2);
                        }
                    }, function(res) {}, false);
                }
            }
        },
        // 获取秘书
        getSecretary() {
            var vm = this;
            if (vm.agent == 'false') {
                vm.post(vm.conf.portalMobile + '/API/WorkFlow/oawf/handlerPerson', {
                    'type': 'travelremark',
                    'token': `${vm.userInfo.ssotoken}L/wp-2q${vm.userInfo.ssotokenCode}`,
                    'queryType': 7
                }, function(res) {
                    let data = res.jsonData;
                    vm.secretaryList = data;
                }, function(res) {}, false);
            }
        }
    }
}
</script>
<style scoped>
.leftWord {
    font-size: 0.32rem!important;
    color: #999999;
}

.rightWord {
    font-size: 0.32rem!important;
    color: #333;
    padding-left: 0.2rem;
}

.lookHistory {
    color: #5eb2ff;
    font-size: 0.32rem!important;
    padding-bottom: 0.26rem;
}

.yd-confirm-hd {
    text-align: center;
}

.remarkLeft {
    /*width: 1rem;*/
    position: absolute;
    top: 0;
    left: 0;
}

.remarkRight {
    width: 6rem;
    overflow: auto;
    word-break: break-all;
    outline: none;
    user-select: text;
    white-space: pre-wrap;
    text-align: left;
    margin-left: 1.6rem;
}

.yd-tabbar {
    padding: 0;
}

.footer {
    width: 100%;
    height: 1rem;
}

.yd-btn-block {
    height: 1rem;
    margin-top: 0;
    border-radius: 0;
    font-weight: bold;
}

.approver_slider {
    margin-bottom: 0;
}

.holiday_two {
    position: relative;
    margin: 0 .17rem;
}

.holiday_three {
    position: relative;
    padding-top: 0.26rem;
    margin: 0 .17rem;
}

.circle01 {
    height: .2rem;
    width: .2rem;
    background-color: #e2e2e2;
    position: absolute;
    top: -0.1rem;
    left: -0.28rem;
    border-radius: 50%;
}

.circle02 {
    height: .2rem;
    width: .2rem;
    background-color: #e2e2e2;
    position: absolute;
    top: -0.1rem;
    right: -0.28rem;
    border-radius: 50%;
}

.holiday_one {
    border-bottom: 1px dashed #eaeaea;
    height: 1rem!important;
}

.otherL {
    margin-top: 0;
    border-right: none;
    height: 1rem;
}

.otherL_agree {
    background-color: #52b1ff;
}

.otherL_disagree {
    background-color: #5388ff;
}

.circulationLogo {
    background-color: #ff5353;
    height: 1rem;
    margin-top: 0;
}

.circulationLogo .yd-flexbox {
    line-height: 1rem;
}

.otherL .yd-flexbox {
    line-height: 1rem;
}

.assistBottom {
    border-top: none;
}

.examineLogo_txt {
    color: #fff;
}

.mainContent {
    height: auto;
    background: #e2e2e2;
    border-top: .11rem solid #e2e2e2;
    border-bottom: .11rem solid #e2e2e2;
}

.contentSradius {
    margin-top: 0;
}

.remarkItem {
    min-height: .6rem;
}

.last_send02 {
    height: 100%;
}

.aproveLine {
    padding-left: 0;
    padding-right: 0;
    margin: 0 .17rem;
}

.title {
    margin-bottom: .17rem;
}

.other {
    border-bottom: 1px dashed #eaeaea!important;
}

.yd-popup-center {
    position: relative;
}

.otherInput {
    height: 4rem;
}

.noRecord {
    font-size: .32rem;
    padding: 0 .2rem;
    height: .5rem;
    line-height: .5rem;
    color: #666;
}

.approName,
.approTime,
.neirongSize {
    font-size: .32rem!important;
}

.approHistory .yd-flexbox {
    align-items: flex-start;
}
</style>


